html {
  background-color: #f6f9fc;
}
body {
  background-color: #f6f9fc;
  visibility: hidden;
}
#scrollButton {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: $scrollbutton-bg;
  color: $scrollbutton-color;
  cursor: pointer;
  padding: 6px;
  border-radius: 50%;
}
#scrollButton:hover {
  background-color: $scrollbutton-bg-hover;
}
blockquote {
  display: block;
  border-width: 2px 0;
  border-style: solid;
  border-color: $border-color;
  padding: 1.5em 0 0.5em;
  margin: 1.5em 0;
  position: relative;
}
blockquote:before {
  content: '\201C';
  position: absolute;
  top: 0em;
  left: 50%;
  transform: translate(-50%, -50%);
  background: $body-bg;
  width: 3rem;
  height: 2rem;
  font-size: x-large;
  color: $body-color;
  text-align: center;
}
